    --foreground: #ededed;
  }
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
}
.reveal {
  opacity: 0;
  transform: translateY(18px);
  transition: opacity .6s ease, transform .8s cubic-bezier(.2,.65,.2,1);
}
.reveal-in {
  opacity: 1;
  transform: translateY(0);
}
